.reforml-form-group {
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
}

.reforml-form-group .flex {
  display: flex;
}

.reforml-form-group .flex1 {
  flex: 1;
}

.reforml-form-group .form-label {
  margin-bottom: .5rem;
}

.reforml-form-group .text-muted {
  color: #6c757d;
}

.reforml-form-group .form-text {
  display: block;
  margin-top: .1rem;
  margin-bottom: .1rem;
}

.reforml-form-group .small, .reforml-form-group small {
  font-size: 80%;
  font-weight: 400;
}

.reforml-form-group .form-control {
  display: block;
  width: 100%;
  padding: .375rem .75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: .25rem;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  box-sizing: border-box;
}

.reforml-form-group input[type=checkbox], .reforml-form-group input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

.reforml-form-group .btn {
  display: inline-block;
  font-weight: 400;
  color: #212529;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.reforml-form-group .btn-danger {
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545;
}

.reforml-form-group .list-input-decoration, .reforml-form-group .object-input-decoration {
  border: 1px #ced4da solid;
  border-radius: .25rem;
}

.reforml-form-group .list-input-decoration .list-input-label, .reforml-form-group .list-input-decoration .object-input-label, .reforml-form-group .object-input-decoration .list-input-label, .reforml-form-group .object-input-decoration .object-input-label {
  padding: .375rem .75rem;
  line-height: 1.5;
  border-bottom: 1px #ced4da solid;
}

.reforml-form-group .list-input-decoration .list-input-create-box, .reforml-form-group .object-input-decoration .list-input-create-box {
  background-color: #e8e8e8;
  padding: 0.5rem 0.3rem;
}

.reforml-form-group .list-input-decoration .list-input-item-box, .reforml-form-group .list-input-decoration .object-item-box, .reforml-form-group .object-input-decoration .list-input-item-box, .reforml-form-group .object-input-decoration .object-item-box {
  border-bottom: 1px #ced4da solid;
  padding: 0.3rem;
}
